import React, { useContext, useEffect, useRef, useState } from "react";
import { gsap } from "gsap";
import "../style/css/buttonsAnim.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faStar as faStarSolid } from "@fortawesome/free-solid-svg-icons";
import { faStar as faStarRegular } from "@fortawesome/free-regular-svg-icons";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";
import { ProductsOrderContext } from "../context/OrderContext";
import { cleanup } from "@testing-library/react";

const ProductBox = (props) => {
	const [order, setOrder] = useContext(ProductsOrderContext);
	const [btnAnim, setBtnAnim] = useState(false);
	const timeoutRef = useRef(null);

	let productImage = useRef(null);

	// product hover animation
	const handleHover = () => {
		gsap.to(productImage, { scale: 1.15, duration: 0.3, ease: "expo" });
	};
	const handleHoverOut = () => {
		gsap.to(productImage, { scale: 1, duration: 0.3, ease: "expo" });
	};

	// print stars
	let printStars = [];
	for (let i = 0; i < props.product.stars; i++) {
		printStars.push(
			<FontAwesomeIcon
				icon={faStarSolid}
				className="highlight"
				key={i + "starSolid"}
			/>
		);
	}
	for (let i = 0; i < 5 - props.product.stars; i++) {
		printStars.push(
			<FontAwesomeIcon
				icon={faStarRegular}
				className="highlight"
				key={i + "starRegular"}
			/>
		);
	}

	const addToCartHandler = () => {
		let { product } = props;
		let newQuantity;
		if (!product.quantity) {
			newQuantity = 1;
		} else if (product.quantity) {
			newQuantity = product.quantity + 1;
		}
		let newItemToCart = product;
		newItemToCart.quantity = newQuantity;

		if (newQuantity === 1) {
			setOrder({
				...order,
				cart: [...order.cart, newItemToCart],
			});
		} else {
			let objIndex = order.cart.findIndex((obj) => obj.name === product.name);

			const updatedCart = order.cart.map((item, index) => {
				return index === objIndex ? { ...item, quantity: newQuantity } : item;
			});
			setOrder({
				...order,
				cart: updatedCart,
			});
		}

		// setBtnAnim(true);
		// useTimeout(() => {
		// 	setBtnAnim(false);
		// }, 700);
	};

	// function useTimeout(callback, delay) {
	// 	const stableCallback = useRef(callback);
	// 	useEffect(() => {
	// 		stableCallback.current = callback;
	// 	}, [callback]);

	// 	useEffect(() => {
	// 		const tick = () => stableCallback.current();
	// 		if (typeof delay !== "number") return;
	// 		const t = setTimeout(tick, delay);
	// 		return () => clearTimeout(t);
	// 	}, [delay]);
	// }

	return (
		<>
			<div
				className="product-box"
				onMouseEnter={handleHover}
				onMouseLeave={handleHoverOut}
			>
				<div className="product-box__product-image-container">
					<img
						// src={productsImages[props.product.img - 1]}
						src={props.product.img}
						alt="product"
						className="product-box__product-image"
						ref={(el) => (productImage = el)}
					/>
				</div>
				<h5 className="product-box__product-name">{props.product.name}</h5>

				<div className="product-box__price-and-buy-btn">
					<span className="product-box__price-span">
						{props.product.price} $
					</span>
					<button
						// className={
						// 	btnAnim
						// 		? "product-box__buy-btn button-anim add-item-to-cart-in"
						// 		: "product-box__buy-btn button-anim"
						// }
						className="product-box__buy-btn button-anim"
						onClick={addToCartHandler}
					>
						Add to cart
					</button>
				</div>
				<div className="product-box__stars-and-opinions-container">
					<div>{printStars}</div>

					<span className="product-box__opinions-span">
						{props.product.opinions} reviews
					</span>
				</div>
				<span className="product-box__read-more">
					READ MORE{" "}
					<FontAwesomeIcon icon={faArrowRight} className="highlight" />
				</span>
			</div>
		</>
	);
};

export default ProductBox;
